import { NextContext } from 'next';
import axios from 'axios';
import { Card, Icon, Avatar } from 'antd';

import TaipeiCardImg from '../../static/taipei_card.png';
import CardLayout from '../../layouts/Card';

import Section from '../../components/Section';

type TUser = {
  did: string;
  name: string;
  certificationName: string;
  certificatedAte: string;
  expiredAt: string;
  issuerName: string;
  transactionHash: string;
  imageChecksum: string;
};

type TProps = {
  user: TUser;
};

function TaiPeiCard({ user }: TProps) {
  return (
    <CardLayout>
      <Section fullscreen>
        <Card
          style={{ width: 300 }}
          cover={
            <img
              alt="Taipei Card"
              src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
            />
          }
        >
          <Card.Meta
            title="Taipei Card"
            description="description #
            quisque tempus vehicula enim. Duis ut dolor nec augue blandit mattis
            in sed lectus. Cras vehicula tincidunt massa, in gravida libero
            fermentum quis."
          />
        </Card>
        ,
      </Section>
    </CardLayout>
  );
}

type TQueryString = {
  did: string;
};

TaiPeiCard.getInitialProps = async ({ query }: NextContext<TQueryString>) => {
  let user: TUser = {
    did: 'did',
    name: 'Chihusan',
    certificationName: 'Course Certification',
    certificatedAte: '2019-03-26',
    expiredAt: '2019-04-05',
    issuerName: 'National Cheng Kung University',
    transactionHash: 'xxx',
    imageChecksum: 'xxxxxx',
  };
  if (query.did) {
    try {
      const res = await axios.get<TUser>(`API${query.did}`);
      user = res.data;
    } catch (err) {
      console.error(err);
    }
  }
  return { user };
};

export default TaiPeiCard;
